.oneBox {
  display: flex;
  color: rgb(251, 247, 255);
  justify-content: center;
  margin-left: 4vw;
  margin-right: 4vw;
  margin-bottom: 3vh;
  margin-top: 3vh;
  background: rgb(31, 0, 48);
  border-radius: 20px ;
  filter: drop-shadow(8px 8px 5px rgb(136, 113, 160));
  padding: 3vh;
}
.columnBox {
  flex-direction: column;

}
.oneBox > img {
  align-self: center;
  margin: 2vw;
}
.oneBox > div {
  align-self: center;
}
.oneBox > div > h1 {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  margin-bottom: 3vh;
  font-weight: bold;
  letter-spacing: 0.5vw;
}
.oneBox > div > h6 {
  font-family: "Montserrat", sans-serif;
  margin-left: 2vw;
  margin-top: 3vh;
}
.oneBox > div > p {
  font-family: "Lato", sans-serif;
  margin-left: 2vw;
  margin-right: 2vw;
  margin-bottom: 0px;
  text-align: justify;
  color: rgb(251, 247, 255);
}
.btn {
  display: flex !important;
  justify-content: center;
  margin: 0px ;
}
.btn-link {
  font-weight: 1000 !important;
  color: rgb(172, 87, 221) !important;
}
.socialMedia {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  position: absolute;
  right: 1vh;
  top: 1vh;
  font-size: 2em;
  margin-right: 1vw;
}
.socialMediaContact {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  font-size: 2.5em;
}

@media (max-width: 375px) {
  .oneBox > div > h1 {
    font-size: 3.5vw;
  }
  .oneBox > div > h6 {
    font-size: 3.25vw ;
  }
  .oneBox > div > p {
    font-size: 3vw ;
  }
  .oneBox {
    flex-direction: column;
  }
  .reverse {
    flex-direction: column-reverse;
  }
  .oneBox > img {
    max-height: 30vh;
    max-width: 80vw;
  }
  .btn-md {
    padding: 5px 5px ;
    font-size: 10px ;
    border-radius: 2px ;
  }
  .socialMedia {
    font-size: 1.5em;
    right: 0.5vh;
    top: 0.5vh;
  }
  .socialMediaContact {
    font-size: 1.75em;
  }
}

@media (min-width: 376px) and (max-width: 759px) {
  .oneBox > div > h1 {
    font-size: 3vw ;
  }
  .oneBox > div > h6 {
    font-size: 2.75vw ;
  }
  .oneBox > div > p {
    font-size: 2.5vw ;
  }
  .oneBox {
    flex-direction: column;
  }
  .reverse {
    flex-direction: column-reverse;
  }
  .oneBox > img {
    max-height: 30vh;
    max-width: 75vw;
  }
  .btn-md {
    padding: 5px 5px ;
    font-size: 11px ;
    border-radius: 2px ;
  }
  .socialMedia {
    font-size: 1.5em;
    right: 0.5vh;
    top: 0.5vh;
  }
  .socialMediaContact {
    font-size: 2em;
  }
}

@media (min-width: 760px) {
  .oneBox > div > h1 {
    font-size: 2vw ;
  }
  .oneBox > div > h6 {
    font-size: 1.75vw ;
  }
  .oneBox > div > p {
    font-size: 1.5vw ;
  }
  .oneBox > img {
    max-height: 40vh ;
    max-width: 40vw ;
  }
}

@media (min-width: 1400px) {
  .oneBox > div > h1 {
    font-size: 1.75vw ;
  }
  .oneBox > div > h6 {
    font-size: 1.5vw ;
  }
  .oneBox > div > p {
    font-size: 1.25vw ;
  }
  .oneBox > img {
    max-height: 40vh ;
    max-width: 40vw ;
  }
}